/* Styles for the ImageModal component */

.image-modal {
    display: none; /* Initially hidden */
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9); /* Semi-transparent black background */
    overflow: auto;
  }
  
  .image-modal-content {
    position: relative;
    margin: auto;
    max-width: 80%;
    max-height: 80%;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 30px;
    color: white;
    cursor: pointer;
  }
  
  .modal-image {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
  }
  