.project-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #333;
}

.project-description {
  font-size: 16px;
  color: #666;
  margin-bottom: 15px;
}

.project-link a {
  display: inline-block;
  color: rgb(10, 10, 10);
  transition: background-color 0.3s ease;
  text-decoration: none;

}

.project-link a:hover {
  transform: translateY(-5px); /* Move the link up by 5 pixels on hover */

 /* background-color: #050505; Darker blue on hover */
}



@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px); /* Adjust the bounce height as needed */
  }
}



@media (max-width: 768px) {
  .project-container {
    padding: 10px;
  }

  .sec-name-p {
    font-size: 20px;
  }

  .cathay-images {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Two columns */
    gap: 20px;
    max-width: 800px;
    margin: 0 auto; /* Center the grid */

  }
  .med-images {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Two columns */
    gap: 20px;
    max-width: 800px;
    margin: 0 auto; /* Center the grid */
  }
  .arrow-link {
    list-style-type: none; /* Remove default list-style */
    position: relative;
    padding-left: 2px; /* Add padding for the arrow */
  }
  
  .arrow-link::before {
    content: '\2192'; /* Unicode arrow right character */
    position: absolute;
    left: 50px;
  }
  
  .arrow-link:hover::before {
    content: '\2192'; /* Unicode double arrow right character on hover */
  }
}
